import React, { useEffect, useRef } from 'react';
import team1 from '../assets/team1.jpg';
import { Helmet } from 'react-helmet';
import './Hakkimizda.css';  

const AboutusDetail = () => {

  const teamSectionRef = useRef(null);
  const aboutTextRef = useRef(null);

  useEffect(() => {
    const teamSection = teamSectionRef.current;
    const aboutText = aboutTextRef.current;

    const observerOptions = {
      root: null, 
      threshold: 0.3,
    };

    const observerCallback = (entries, observer) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          if (entry.target === teamSection) {
            entry.target.style.opacity = '1';
            entry.target.style.transform = 'translateY(0)';
          } else if (entry.target === aboutText) {
            entry.target.style.opacity = '1';
            entry.target.style.transform = 'translateY(0)';
          }
          observer.unobserve(entry.target);
        }
      });
    };

    const observer = new IntersectionObserver(observerCallback, observerOptions);

    if (teamSection) observer.observe(teamSection);
    if (aboutText) observer.observe(aboutText);

    return () => {
      if (teamSection) observer.unobserve(teamSection);
      if (aboutText) observer.unobserve(aboutText);
    };
  }, []);

  return (
    <>
      <Helmet>
        <title>Hakkimizda - Ankara Avukat</title>
        <meta name="description" content="Ankara'da avukatlik ve danimanlik hizmeti sunan hukuk bromuz hakkinda bilgi." />
        <meta name="keywords" content="Ankara, boanma avukati ankara, ceza avukati ankara, avukat, danismanlik, hukuk, mvekkil memnuniyeti, Ankara avukat, ankaya avukat" />
        <meta name="author" content="Avukat Ayenur Konar" />
      </Helmet>

      <section >
        <div id='ankara-avukat-hakkimizda' className="content">
          <div ref={teamSectionRef} className="teamSection">
            <h2 className="founder-title">Kurucumuz</h2>
            <div className="teamMember">
              <img src={team1} alt="Avukat Ayenur Konar" />
              <h3 className='team-member-pg-name'>Av. Ayenur Konar</h3>
              <p>Ankara Barosu - 37181</p>
            </div>
          </div>

          <div ref={aboutTextRef} className="aboutText">
            <h2>Hakkmzda</h2>
            <p>
            Biz, Ankara'da avukatlk ve danmanlk hizmeti veren, kaliteli hizmet ve mvekkil memnuniyetini her zaman en n planda tutan, mvekkil odakl bir hukuk brosuyuz. Yllarn getirdii tecrbe ve bilgi birikimiyle, her zaman en iyi zmleri sunmak iin titizlikle alyoruz. Ankara'daki avukatlk ve danmanlk ihtiyalarnz en iyi ekilde anlayarak, size yeniliki, etkili ve srdrlebilir hizmetler salyoruz. Hukuk alanndaki geni uzmanlk yelpazemiz ile; ticaret hukuku, aile hukuku, i hukuku, miras hukuku, ceza hukuku, gayrimenkul hukuku, icra iflas hukuku, vergi hukuku, idare hukuku, szleme hukuku, borlar hukuku, salk hukuku, ii haklar, ipotekli tanmazlar, iflas erteleme, boanma davalar, nafaka davalar, mal paylam davalar gibi birok alanda profesyonel destek sunuyoruz.
            Ankara avukat araynzda, hukuk brosu, hukuk danmanlk hizmeti ve hizmet ii danmanlk konularnda da sizlere yardmc olmak iin buradayz. Alannda uzman aile avukat, ticaret avukat, ceza avukat, gayrimenkul avukat ve icra avukat gibi deneyimli kadromuzla, size zel hukuki zmler sunuyoruz. stanbul avukat ya da Trkiye genelinde avukat aryorsanz da, profesyonel hizmetlerimizi geni bir alanda sunmaktayz.
            Her bir mvekkilimizin zel gereksinimlerine ynelik zmler gelitirmeye zen gsteriyor, onlarn beklentilerini amay amalyoruz. Avukatlk hizmetlerimizde daima gven, drstlk ve gizlilii n planda tutarak, mvekkillerimize adaletin en hzl ve doru ekilde tecelli etmesini salyoruz.
            Hukuki danmanlk almak, hukuk davas amak, boanma davas gibi ilemler iin avukata bavurmak isteyenler, ceza davalar, i kazas davalar, gayrimenkul alm satm, haksz fiil davalar, kira szlemesi sorunlar gibi her trl hukuki ilem iin bizimle iletiime geebilirler. Hukuki hizmet konusunda uzmanlam Ankara avukatlar olarak, i davalar, ticaret davalar, ceza davalar, miras davalar gibi geni bir yelpazede hukuki sreleri en etkili ekilde ynetiyoruz.
            Eer siz de profesyonel avukatlk hizmeti aryorsanz, Ankara boanma avukat, hukuk danman, aile hukuku avukat, ticaret hukuku avukat, ceza hukuku danman, gayrimenkul avukat, ii avukat, vergi avukat, miras hukuku avukat, borlar hukuku avukat gibi alanlarda destek almak iin bizimle iletiime geebilirsiniz. Ankara'da avukat aray iindeyseniz, uzman kadromuz size en iyi hizmeti sunmak iin hazr. 
            Daha fazla bilgi iin bizimle iletiime gein ve ihtiyalarnza ynelik en doru hukuki zmleri bulalm. Unutmayn, Konar Hukuk Ankara avukatlar olarak tm hukuk srelerinizi profesyonel bir yaklamla ynetiyoruz. Hukuki haklarnz iin doru adres biziz!
            </p>
          </div>
        </div>
      </section>
    </>
  );
};

export default AboutusDetail;
