import React from 'react';
import './ServicesDetail.css';
import { Helmet } from 'react-helmet';

const CustomCard = ({ image, title, alt }) => {
  return (
   
    <div className="custom-card">
      <img src={image} alt={alt} />
      <h3>{title}</h3>
    </div>
  );
  };

const ServicesDetail = () => {
  const servicesdetailed = [
    { title: "Ceza Hukuku Hizmeti", image: "images/ceza_hukuku.jpg", alt: "Ankara Ceza hukuku avukati - Savunma ve sucluluk durumlari" },
    { title: "Aile Hukuku Hizmeti", image: "images/aile_hukuku.jpg", alt: "Ankara Aile hukuku avukati - Bosanma, velayet, nafaka" },
    { title: " Hukuku Hizmeti", image: "images/is_hukuku.jpg", alt: "Ankara Is hukuku avukati - Calisan haklari ve is sozlesmeleri" },
    { title: "Ticaret Hukuku Hizmeti", image: "images/ticaret_hukuku.jpg", alt: "Ankara Ticaret hukuku avukati - Sirket kurma ve ticaret sozlesmeleri" },
    { title: "cra ve flas Hukuku Hizmeti", image: "images/icra_iflas_hukuku.jpg", alt: "Ankara Icra ve iflas hukuku avukati - Borc odeme ve alacak tahsili" },
    { title: "Gayrimenkul Hukuku Hizmeti", image: "images/gayrimenkul_hukuku.jpg", alt: "Ankara Gayrimenkul hukuku avukati - Satis, kira ve tapu islemleri" },
    { title: "Tketici Hukuku Hizmeti", image: "images/tuketici_hukuku.jpg", alt: "Ankara Tuketici hukuku avukati - Satis alma, garanti ve cayma hakki" },
    { title: "Vergi Hukuku Hizmeti", image: "images/vergi_hukuku.jpg", alt: "Ankara Vergi hukuku avukati - Vergi odemeleri ve yasal duzenlemeler" },
    { title: "Miras Hukuku Hizmeti", image: "images/miras_hukuku.jpg", alt: "Ankara Miras hukuku avukati - Miras paylasimi ve vasiyetname islemleri" },
    { title: "Salk Hukuku Hizmeti", image: "images/saglik_hukuku.jpg", alt: "Ankara Saglik hukuku avukati - Saglik hizmetleri, tibbi sorumluluk" },
    { title: "Fikri Mlkiyet Hukuku Hizmeti", image: "images/fikri_mulkiyet_hukuku.jpg", alt: "Ankara Fikri mulkiyet hukuku avukati - Patent, telif ve marka haklari" },
    { title: "Uluslararas Hukuk Hizmeti", image: "images/uluslararasi_hukuk.jpg", alt: "Ankara Uluslararasi hukuk avukati - Sinir otesi anlasmazliklar ve sozlesmeler" }
  ];

  return (
    <>
    <Helmet>
            <title>alma Alanlarmz - Ankara Avukat</title>
            <meta name="description" content="Ankara'da avukatlik ve danimanlik hizmeti sunan hukuk bromuz hakkinda bilgi." />
            <meta name="keywords" content="Ankara, avukatlik, danismanlik, hukuk, mvekkil memnuniyeti" />
            <meta name="author" content="Avukat Ayenur Konar" />
          </Helmet>
    
    <div id='ankara-avukat-calisma-alanlari' className='service-detailed-container'>
      <h2>alma Alanlarmz</h2>
      {servicesdetailed.map((service, index) => (
        <CustomCard
          key={index}
          image={service.image}
          title={service.title}
          alt={service.alt}
        />
        
      ))}
      
    </div>
    </>

  );
  
};

export default ServicesDetail;
