export default function BlogDetail() {
  const { id } = useParams();
  const blog = blogData.find(b => b.id === parseInt(id));

  const [headings, setHeadings] = useState([]);
  const ref = useRef(null);

  useEffect(() => {
    if (blog) {
      const headingTags = [];
      const contentContainer = ref.current;
      const headingsList = contentContainer.querySelectorAll('h3, h4');
      headingsList.forEach(tag => {
        headingTags.push({
          id: tag.id,
          text: tag.innerText,
          tag: tag.tagName,
        });
      });
      setHeadings(headingTags);
    }
  }, [blog]);

  if (!blog) {
    return <div className="not-found">Blog not found</div>;
  }

  return (
    <div className="blog-detail-container">
      <Helmet>
        <title>{blog.title} - Blog</title>
        <meta name="description" content={`Detayl bilgi iin ${blog.title} sayfasn ziyaret edin.`} />
        <meta name="keywords" content="Aile Hukuku, Miras Hukuku, Ceza Hukuku, Hukuk, Boanma, ocuk Haklar" />
        <meta name="author" content="Av.Ayenur Konar" />
      </Helmet>

      <h2 className="blog-detail-title">{blog.title}</h2>

      <div className="blog-detail-card">
        <img src={blog.image} alt={blog.title} className="blog-detail-image" />
        <div
          className="blog-detail-firstcontent"
          dangerouslySetInnerHTML={{ __html: blog.firstcontent }}
        />
      </div>

      {headings.length > 0 && (
        <div className="table-of-contents">
          <h5>indekiler</h5>
          <ul>
            {headings.map((heading, index) => (
              <li key={index}>
                <a href={`#${heading.id}`}>{heading.text}</a>
              </li>
            ))}
          </ul>
        </div>
      )}
      <div
        className="blog-detail-content"
        ref={ref}
        dangerouslySetInnerHTML={{ __html: blog.content }}
      />
      <div className="signature">
        <h5>Av.Ayenur Konar</h5>
      </div>
    </div>
  );
}
