import React, { useEffect, useRef } from 'react';
import './Aboutus.css';
import team1 from '../assets/team1.jpg';
import { Helmet } from 'react-helmet'; 

const Aboutus = () => {

  const teamSectionRef = useRef(null);
  const aboutTextRef = useRef(null);

  useEffect(() => {
    const teamSection = teamSectionRef.current;
    const aboutText = aboutTextRef.current;

    const observerOptions = {
      root: null, 
      threshold: 0.3, 
    };

   
    const observerCallback = (entries, observer) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          if (entry.target === teamSection) {
            entry.target.classList.add('visible-team'); 
          } else if (entry.target === aboutText) {
            entry.target.classList.add('visible-about');
          }
          observer.unobserve(entry.target);
        }
      });
    };

    
    const observer = new IntersectionObserver(observerCallback, observerOptions);

    if (teamSection) observer.observe(teamSection);
    if (aboutText) observer.observe(aboutText);

   
    return () => {
      if (teamSection) observer.unobserve(teamSection);
      if (aboutText) observer.unobserve(aboutText);
    };
  }, []);

  return (
<>
  <Helmet>
    <meta name="description" content="Ankara'da avukatlik ve danimanlik hizmeti sunan hukuk bromuz hakkinda bilgi." />
    <meta name="keywords" content="Ankara, avukat, Bosanma avukati,danismanlik, hukuk, Avukat Aysenur Konar, mvekkil memnuniyeti, ankara, ankaya" />
    <meta name="author" content="Avukat Ayenur Konar" />
  </Helmet>

    <section id="ankara-avukat-hakkimizda-home" className="aboutus">
      <div className="aboutus-content">
        <div ref={teamSectionRef} className="team-section">
          <h2 className="founder-title">Kurucumuz</h2>
          <div className="team-member">
            <img src={team1} alt="Avukat Ayenur Konar" />
            <h3 className='team-member-name-home'>Av. Ayenur Konar</h3>
            <p className='team-member-no-home'>Ankara Barosu - 37181</p>
          </div>
        </div>

    
        <div ref={aboutTextRef} className="about-text">
          <h2>Hakkmzda</h2>
          <p className='about-text-home-p'>
          Biz, Ankara'da avukatlk ve danmanlk hizmeti veren, kaliteli hizmet ve mvekkil memnuniyetini en n planda tutan, mvekkil odakl bir hukuk brosuyuz. Yllarn getirdii tecrbe ve bilgi birikimiyle, her zaman en iyi zmleri sunmak iin titizlikle alyoruz. Ankara'daki avukatlk ve danmanlk ihtiyalarnz en iyi ekilde anlayarak, size yeniliki, etkili ve srdrlebilir hizmetler salyoruz. Her bir mvekkilimizin zel gereksinimlerine ynelik zmler gelitirmeye zen gsteriyor, onlarn beklentilerini amay amalyoruz. Takmmz, her zaman yksek kalite standartlarna sadk kalarak, Ankara'da sektrdeki lider konumumuzu korumak ve daha da glendirmek iin srekli olarak kendini gelitiriyor. Amacmz, Ankara'da sektrdeki en gvenilir ve saygn hukuk brosu haline gelmek, mvekkil memnuniyetini srekli olarak artrarak uzun vadeli i ilikileri kurmaktr. Her projede mkemmeliyetin peinden gidiyor ve Ankara'daki mvekkillerimize deer katmay hedefliyoruz. Bu hedefe ulaabilmek iin, mvekkil geri bildirimlerine byk nem veriyor, her bir gr ve neriyi i srelerimizi iyiletirmek iin bir frsat olarak deerlendiriyoruz. Mvekkillerimizin taleplerine hzl ve etkin zmler reterek, onlarn zamanna ve kaynaklarna deer katyoruz. Bizim iin her mvekkil, yalnzca bir i ilikisi deil, ayn zamanda uzun sreli bir ortaklktr. Bu nedenle, her projede yalnzca sonu odakl deil, ayn zamanda srecin her aamasnda kaliteli bir deneyim sunmay hedefliyoruz. Srekli geliim ve inovasyon kltrmz, bizi Ankara'da sektrdeki en gvenilir ve saygn hukuk brosu olma yolunda ileriye tayor. Bu deerlerimizle, mvekkil odakl alma prensiplerimizi daha da glendirerek, sektrdeki liderliimizi pekitirmeyi srdreceiz.
          </p>
        </div>
      </div>
    </section>
    </>
  );
};

export default Aboutus;
